.layout {
  position: relative;
  display: flex;
  flex: 1 1 auto;
  width: 100%;
  height: 100%;
}
.vertical-layout,
.horizontal-layout {
  display: flex;
  flex: 1 1 auto;
  width: 100%;
  height: 100%;
}
.vertical-layout .content,
.horizontal-layout .content {
  width: 100%;
  display: flex;
  flex-direction: column;
  justify-content: space-between;
  transition: all 250ms ease;
}
.vertical-layout .content {
  margin-top: 4.375rem;
}
@media only screen and (min-width: 992px) {
  .vertical-layout .content.is-collapse {
    margin-left: 4rem;
    width: calc(100% - 4rem);
  }
}
@media only screen and (min-width: 992px) and (max-width: 1440px) {
  .vertical-layout .content {
    margin-left: 14.375rem;
    width: calc(100% - 14.375rem);
  }
}
@media only screen and (min-width: 1440px) {
  .vertical-layout .content {
    margin-left: 17.5rem;
    width: calc(100% - 17.5rem);
  }
}
.vertical-layout .content .main {
  padding: 1.5625rem;
}
.vertical-layout .content .content-min-height {
  min-height: calc(100vh - 4.375rem - 1.5625rem - 1.5625rem - 4.0625rem - 2px);
}
.vertical-layout .content .content-max-height {
  max-height: calc(100vh - 4.375rem - 1.5625rem - 1.5625rem - 4.0625rem - 2px);
}
.horizontal-layout .content {
  margin-top: 4.375rem;
}
